<template>
	<div id="statcenter-paytype">
		<public-header title="支付方式统计"></public-header>
		<div class="content">
			<div class="padding-15">
				<span class="font-16">{{store.title ? store.title : '全部店铺'}}</span>
				<span class="c-gray font-14">{{filter.items && filter.items.stat_day ? filter.items.stat_day.start + '-' + filter.items.stat_day.end : '今天'}}</span>
			</div>
			<van-cell-group>
				<van-cell title="总订单" :value="stat.total_success_order" />
				<van-cell title="货到付款单数/占比" :value="stat.total_delivery_order + '/' + stat.per_total_delivery_order + '%'" />
				<van-cell title="余额支付单数/占比" :value="stat.total_credit_order + '/' + stat.per_total_credit_order + '%'" />
				<van-cell title="微信支付单数/占比" :value="stat.total_wechat_order + '/' + stat.per_total_wechat_order + '%'" />
				<van-cell title="支付宝支付单数/占比" :value="stat.total_alipay_order + '/' + stat.per_total_alipay_order + '%'" />
			</van-cell-group>
			<div class="padding-10">
				<van-button type="primary" size="normal" block class="font-16" @click="util.jsUrl('pages/statcenter/paytypeDetail')">查看详情</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				store: {}
			}
		},
		methods: {
			...mapMutations([
				'setSearch'
			]),

			onLoad() {
				let that = this;
				getData({
					vue: that,
					data: {
						filter: that.filter ? that.filter.items: {}
					},
					/*autoAssign: true,
					variable: 'stat',*/
					url: 'plateform/statcenter/paytype/index',
					success: function(result) {
						that.stat = result.stat;
						that.store = result.store;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>

</style>


